}

.own-title {
  text-align: center;
  font-size: 50px;
  font-style: italic;
  padding: 50px 0;
  color: rgb(113, 12, 122);
}

.container {
  margin: 0 auto;
  min-width: 20%;
  max-width: 1600px;
}

.header .top_nav {
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding-top: 20px;
  padding-bottom: 20px;
}
.header .top_nav .logo {
  text-transform: uppercase;
  font-size: 18px;
  color: rgb(113, 12, 122);
  font-style: italic;
  font-weight: 600;
}
.header .top_nav .line__wrapper {
  padding: 7px 20px 7px 1px;
  cursor: pointer;
}
.header .top_nav .line {
  display: none;
  width: 20px;
  height: 2px;
  background: #000;
  position: relative;
  transition: 0.5s;
}
.header .top_nav .line::before {
  content: "";
  width: 20px;
  height: 2px;
  display: block;
  background: #000;
  position: absolute;
  top: -6px;
  transition: 0.5s;
}
.header .top_nav .line::after {
  content: "";
  width: 20px;
  height: 2px;
  display: block;
  background: #000;
  position: absolute;
  top: 6px;
  transition: 0.5s;
}
.header .top_nav .line2 {
  width: 20px;
  height: 2px;
  display: block;
  background: #000;
  position: absolute;
  transform: rotate(45deg);
  transition: 0.5s;
}
.header .top_nav .line2::after {
  content: "";
  width: 20px;
  height: 2px;
  display: block;
  background: #000;
  position: absolute;
  transform: rotate(-90deg);
  transition: 0.5s;
}
.header .top_nav ul {
  display: flex;
  transition: 0.5s;
}
.header .top_nav ul .social-item,
.header .top_nav ul .nav-item {
  padding-left: 15px;
  padding-right: 15px;
}
.header .top_nav ul .social-item .link,
.header .top_nav ul .nav-item .link {
  text-decoration: none;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 400;
  font-style: italic;
  color: rgb(37, 36, 36);
  transition: all 0.3s ease-in-out;
}
.header .top_nav ul .social-item .link:hover,
.header .top_nav ul .nav-item .link:hover {
  color: rgb(113, 12, 122);
}
.header .top_nav ul .social-item .facebook:hover,
.header .top_nav ul .nav-item .facebook:hover {
  box-shadow: 0px 0px 5px 5px #0920f3;
  background-color: #0920f3;
  color: #fff;
}
.header .top_nav ul .social-item .instagram:hover,
.header .top_nav ul .nav-item .instagram:hover {
  box-shadow: 0px 0px 5px 5px #f33809;
  background-color: #f30963;
  color: #fff;
}
.header .social ul {
  display: flex;
}
.header .social ul li {
  padding-left: 15px;
  padding-right: 15px;
}
.header .social ul li a {
  font-size: 16px;
  color: rgb(37, 36, 36);
}

.top__slider {
  width: 100%;
}
.top__slider .item {
  display: flex;
  justify-content: center;
  align-items: center;
}
.top__slider .item1 {
  background-color: #0920f3;
}
.top__slider .item2 {
  background-color: #f34b09;
}
.top__slider .item3 {
  background-color: #09f34f;
}
.top__slider .slider__track {
  display: flex;
  overflow: hidden;
  width: 100%;
  height: 100%;
}
.top__slider .slider__track figure {
  width: 100%;
  position: relative;
}
.top__slider .slider__track figure img.slide {
  max-width: 100%;
  height: auto;
  transform: translate(0);
}
.top__slider .slider__track figure .figcaption {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.top__slider .slider__track figure .figcaption .info {
  max-width: 900px;
  color: #fff;
  text-align: center;
}
.top__slider .slider__track figure .figcaption .info span {
  font-size: 22px;
  text-transform: uppercase;
}
.top__slider .slider__track figure .figcaption .info h1 {
  padding-top: 40px;
  font-size: 40px;
  font-weight: 700;
}

.about {
  margin: 40px 0;
}
.about .about__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
}
.about .about__left .mision {
  padding-top: 4%;
  font-size: 18px;
  line-height: 18px;
}
.about .about__left p {
  font-size: 18px;
  line-height: 18px;
}
.about .about__left p span {
  color: rgb(113, 12, 122);
  font-weight: 700;
}
.about .icon {
  min-width: 20%;
  max-width: 100%;
  height: 100%;
  margin: 0 20px;
}
.about .about_right {
  text-align: center;
  border: 1px solid rgb(206, 199, 199);
}
.about .about_right .title-about {
  font-size: 35px;
  padding: 80px 50px 30px 50px;
  font-style: italic;
  color: rgb(113, 12, 122);
}
.about .about_right p {
  font-size: 20px;
  font-weight: 700;
  padding-bottom: 5%;
}

.service .our__service .our__service-top {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-wrap: wrap;
}
.service .our__service .haircut {
  width: 25%;
  height: auto;
  margin: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  text-align: center;
  background: rgba(253, 245, 250, 0.966);
}
.service .our__service .haircut img {
  width: 25%;
  margin: 20px auto;
}
.service .our__service .haircut span {
  font-size: 20px;
  font-weight: 700;
  text-decoration: underline;
  color: rgb(113, 12, 122);
  padding: 0 40px;
}
.service .our__service .haircut p {
  padding: 50px 25px;
  color: rgb(151, 148, 148);
}

section.master .team {
  background: rgba(253, 245, 250, 0.966);
