}

.works .work__arrow .our__works {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.works .work__arrow .our__works .image__work {
  width: 25%;
  margin: 20px;
  height: auto;
}

.footer {
  margin-top: 150px;
  background: rgb(71, 65, 65);
  color: #fff;
}
.footer .footer_contact {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 50px 11%;
}
.footer .footer_contact nav.menu_footer ul {
  display: flex;
}
.footer .footer_contact nav.menu_footer ul li a {
  color: rgb(175, 172, 172);
  font-size: 22px;
  padding-left: 40px;
  transition: 0.5s;
}
.footer .footer_contact nav.menu_footer ul li .inst:hover {
  color: #c26a06;
}
.footer .footer_contact nav.menu_footer ul li .face:hover {
  color: #0920f3;
}
.footer .footer_contact nav.menu_footer ul li .vk:hover {
  color: #5868f3;
}
.footer .footer_contact nav.menu_footer ul li .twit:hover {
  color: #929bf0;
}
.footer .footer_contact .top h2 {
  font-size: 22px;
  padding-bottom: 10px;
}
.footer .footer_contact .top .title {
  padding: 10px;
}
.footer .footer_contact .top a {
  padding: 10px;
  color: rgb(243, 230, 230);
  text-decoration: none;
  transition: 0.5s;
}
.footer .footer_contact .top a:hover {
  color: #ec9b9b;
}
.footer .footer_contact .top .phone {
  padding: 10px;
}

@media screen and (max-width: 1024px) {
  .about__wrap {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .about__wrap .icon {
    max-width: 90%;
    padding: 30px;
  }
  .service .our__service .our__service-top {
    padding: 40px 15%;
  }
  .service .our__service .our__service-top .haircut {
