  .header .top_nav {
    display: flex;
    align-items: flex-start;
  }
  .header .top_nav .menu .laptop__nav {
    display: none;
  }
  .header .top_nav .menu .laptop__nav-block {
    display: block;
    padding: 5px;
    background-color: #fff;
    box-shadow: 0px 0px 1px 0.1px #000;
    transition: 3s;
  }
  .header .top_nav .menu .laptop__nav-block .nav-item {
    margin: 10px;
    transition: 0.5s;
  }
  .header .top_nav .line {
    display: block;
  }
  .header .top_nav .social .social-item {
    padding: 0 5px;
  }
}
@media screen and (max-width: 690px) {
  .footer .footer_contact {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .footer .footer_contact nav.menu_footer {
    padding-top: 40px;
  }
  .footer .footer_contact nav.menu_footer li a {
    padding: 50px;
  }
  .about .about_right .title-about {
    font-size: 16px;
  }
}
@media screen and (max-width: 490px) {
  .own-title {
    font-size: 30px;
  }
  .service .our__service .our__service-top {
    padding: 20px 10%;
  }
